import React from 'react';
import ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import './DateAndQuotation.scss';
import classNames from 'classnames';
import { DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main style file
import 'react-date-range/dist/theme/default.css'; // theme css file
import {bindActionCreators} from 'redux';
import {onDateChange} from '../../../Home/modules/home.js';

class DateAndQuotation extends React.Component{
  constructor(props){
    super(props);

    var today = new Date();
    var tomorrow = new Date();
    var dayAfter = new Date();
    tomorrow.setDate(today.getDate()+1);
    dayAfter.setDate(tomorrow.getDate()+1);
    this.state = {
        startDate: tomorrow,
        endDate: dayAfter,
        startDateString: this.getDateString(tomorrow),
        endDateString: this.getDateString(dayAfter),
        endDateYearString: this.getYearString(dayAfter),
        days: 2,
        showDatePicker: false,
        minDate: new Date(),
        maxDate: new Date()
    };

  }

  componentDidMount(){
    this.calculateMinMaxDate();
  }



  getDateString(date) {
      var str = date.toDateString();
      return `${str.substring(0, 3)}, ${str.substring(8,10)} ${str.substring(4,7)}`;
  }

  getYearString(date) {
      return `${date.getFullYear()}`;
  }

  handleSelect = (ranges) => {
      var startDate = ranges.selection.startDate;
      var endDate = ranges.selection.endDate;
      var days = this.subtractDate(startDate, endDate) + 1;
      this.setState(
          {
              startDate: startDate,
              endDate: endDate,
              startDateString: this.getDateString(startDate),
              endDateString: this.getDateString(endDate),
              endDateYearString: this.getYearString(endDate),
              days: days
          }
      );
      this.props.actions.onDateChange(startDate,endDate);
  }

  calculateMinMaxDate(){
    var today = new Date();
    var deadline = today.setHours(18,0,0,0);
    if(today > deadline){
      const minDate = today.setDate(today.getDate()+2);
      const maxDate = today.setDate(today.getDate()+31)
      this.setState({minDate:new Date(minDate),maxDate:new Date(maxDate)});
      console.log('minDdate: ',new Date(minDate));
      console.log('maxDate: ',new Date(maxDate));
    }
    else{
      const minDate = today.setDate(today.getDate()+1);
      const maxDate = today.setDate(today.getDate()+30)
      this.setState({minDate:new Date(minDate),maxDate:new Date(maxDate)});
      console.log('minDdate: ',new Date(minDate));
      console.log('maxDate: ',new Date(maxDate));
    }

  }

  openDatePicker = () => {
      this.setState({showDatePicker: true});
  }

  closeDatePicker = () => {
      this.setState({showDatePicker: false});
  }

  subtractDate(date1, date2) {
      var one_day=1000*60*60*24;

      var date1_ms = date1.getTime();
      var date2_ms = date2.getTime();

      var difference_ms = date2_ms - date1_ms;

      return Math.round(difference_ms/one_day);
  }

  getDateString(date) {
      var str = date.toDateString();
      return `${str.substring(0, 3)}, ${str.substring(8,10)} ${str.substring(4,7)}`;
  }

  getYearString(date) {
      return `${date.getFullYear()}`;
  }

  calculateQuote(days){
    const firstTwoPrice = 6;
    const secondTwoPrice = 4;
    const restDaysPrice = 2;
    if(days>4){
      const rest = days - 4;
      return (rest*restDaysPrice + 2*secondTwoPrice + 2*firstTwoPrice);
    }
    else if(days>2){
      const rest = days - 2;
      return (rest*secondTwoPrice + 2*firstTwoPrice);
    }
    else{
      return days*6;
    }
  }

  componentWillReceiveProps({dates}){
    const days = this.subtractDate(dates.startDate,dates.endDate) + 1;
    this.setState({startDate:dates.startDate, endDate:dates.endDate,days});

  }

  render(){
    const {startDate, endDate, days, startDateString, endDateString, endDateYearString, showDatePicker, promoCode} = this.state;
    const selectionRange = {
        startDate: startDate,
        endDate: endDate,
        key: 'selection',
    };
  return(
    <div className="pick-date-section">
      <label id="title1" className="dq-title-text">Covered Dates</label>
      <label id="title2" className="dq-title-text">Total Cost</label>
      <p className="date-time clickable" onClick={this.openDatePicker}>{this.getDateString(this.state.startDate)} <span>to </span>
        {this.getDateString(this.state.endDate)} <span>{this.getYearString(this.state.endDate)}</span></p>
      <div className={classNames({"d-none": !showDatePicker})}>
        <div className="date-popup-narrow"></div>
        <DateRangePicker
            ranges={[selectionRange]}
            onChange={this.handleSelect}
            months={2}
            direction="horizontal"
            showMonthAndYearPickers={false}
            showDateDisplay={false}
            minDate={this.state.minDate}
            maxDate={this.state.maxDate}
        />
        <div className="date-picker-footer">
            <button type="button" className="btn btn-close" onClick={this.closeDatePicker}>Close</button>
        </div>
        <div className="date-range-picker-modal" onClick={this.closeDatePicker}>
        </div>
      </div>

          <button className="btn btn-green btn-change-date" onClick={this.openDatePicker}>CHANGE DATE</button>

      <p className="money">S${this.calculateQuote(days)}.00</p>
      <div className="green-line">
      <hr/>
      </div>
      <p className="dq-detail-text">
          <strong>{days} Day Coverage</strong><br/>
          Cover 60% of Your Fare<br/>
          <span>Maximum Claim for {days} days:<br/>
              S${days*50} (up to S$50/day)</span>
      </p>
    </div>
  );
  }
}

function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators({onDateChange}, dispatch),
    };
}

const mapStateToProps = (state) => ({
    dates:state.dates
});

export default connect(mapStateToProps, mapDispatchToProps)(DateAndQuotation);



